.slideContent {
  display: flex;

  @media(max-width: 592px) {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.textContent {
  width: 60%;
  padding: 20px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1.2em;

  @media(max-width: 592px) {
    width: 80%;
    text-align: center;
  }

}

.slideHeader {
  margin: 0 0 20px 0;
}

.slideText {
  font-style: italic;
}

.ratingWrapper {
  width: 100%;
  margin: 20px 0 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rating {
  display: flex;
  flex-direction: row-reverse;
  color: gray;
}

.rating[data-total-value='1'] .ratingItem:nth-child(n + 5),
.rating[data-total-value='2'] .ratingItem:nth-child(n + 4),
.rating[data-total-value='3'] .ratingItem:nth-child(n + 3),
.rating[data-total-value='4'] .ratingItem:nth-child(n + 2),
.rating[data-total-value='5'] .ratingItem:nth-child(n + 1) {
  color: #004F44;
}

.ratingItem {
  font-size: 1.5em;
}

.imgWrapper {
  width: 30%;
  padding: 0 0 40% 0;
  position: relative;

  @media(max-width: 592px) {
    width: 60%;
    padding: 0 0 80% 0;
  }
}

.img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
  object-fit: cover;
}